                    :current-index="currentIndex"
                    v-show="isShowNavBar"/> <!--  导航头  -->

    <!--    content必须有固定高度-->
    <scroll class="content" ref="scroll" :probe-type="3" @scroll="contentScroll">
      <detail-swiper :top-images="topImages" class="to-goods" ref="base"/>  <!--  轮播图  -->
      <detail-base-info :goods="goods"/>   <!-- 商品详情区域 -->
      <detail-shop-info :shop="shop"/> <!-- 店铺信息 -->
      <detail-goods-info :detail-info="detailInfo"
                         @imageLoad="imageLoad"/> <!--   商品详情   -->
      <detail-param-info :param-info="paramInfo" class="to-param" ref="param"/>  <!--   商品参数   -->
      <detail-comment-info :comment-info="commentInfo" class="to-comment" ref="comment"/>  <!--   商品评论   -->
      <goods-list :goods="recommends" class="to-recommends" ref="recommends"/> <!--   商品推荐   -->
