    <back-top @click.native="backClick" v-show="isShowBackTop"/>   <!-- 回到顶部 -->
<!--    <toast :message="message" :show="show"></toast>-->
  </div>
</template>

<script>
  import DetailNavBar from "./childcomps/DetailNavBar"; // 导航头组件
  import DetailSwiper from "./childcomps/DetailSwiper"; // 轮播图组件
  import DetailBaseInfo from "./childcomps/DetailBaseInfo"; // 商品详情
  import DetailShopInfo from "./childcomps/DetailShopInfo"; // 店铺详情
  import DetailGoodsInfo from "./childcomps/DetailGoodsInfo"; // 商品详情
  import DetailParamInfo from "./childcomps/DetailParamInfo"; // 商品参数
  import DetailCommentInfo from "./childcomps/DetailCommentInfo"; // 评论
  import GoodsList from "components/content/goods/GoodsList";  // 商品推荐
  import DetailBottomBar from "./childcomps/DetailBottomBar"; // 底部导航栏

  import Scroll from 'components/common/scroll/Scroll'
  import BackTop from 'components/content/backtop/BackTop'  // 回到顶部组件
  import { debounce } from 'common/utils'
  import { mapActions } from 'vuex'

  // import Toast from "components/common/toast/Toast";

  // 网络请求
  import { getDetail, Goods, Shop, GoodsParam, getRecommend } from 'network/detail.js'

  export default {
    name: "Detail",
    components: {
      DetailNavBar,
      DetailSwiper,
      DetailBaseInfo,
      DetailShopInfo,
      Scroll,
