      // 点击导航标签滚动到指定位置
      titleClick(index) {
          switch (index){
            case 0:
              this.$refs.scroll.scroll.scrollToElement('.to-goods', 300)
              break
            case 1:
              this.$refs.scroll.scroll.scrollToElement('.to-param', 500)
              break
            case 2:
              this.$refs.scroll.scroll.scrollToElement('.to-comment', 300)
              break
            case 3:
              this.$refs.scroll.scroll.scrollToElement('.to-recommends', 300)
              break
          }
        // this.$refs.scroll.scroll.scrollTo(0, -this.themeTopYs[index], 100)
        console.log(index)
      },

      // 监听滚动内容显示对应标题
      contentScroll(position) {
        // 1.判断BackTop、顶部NavBar是否显示
        this.isShowBackTop = (-position.y) > 1000
        this.isShowNavBar = (-position.y) > 50
        // 2.监听滚动到哪一个主题
        // this._listenScrollTheme(-position.y)
      },
      // _listenScrollTheme(position) {
      //   // const positionY = -position.y
      //   let length = this.themeTopYs.length;
      //   for (let i = 0; i < length; i++) {
      //     let iPos = this.themeTopYs[i];
      //     /**
      //      * 判断的方案:
      //      *  方案一:
      //      *    条件: (i < (length-1) && currentPos >= iPos && currentPos < this.themeTops[i+1]) || (i === (length-1) && currentPos >= iPos),
      //      *    优点: 不需要引入其他的内容, 通过逻辑解决
      //      *    缺点: 判断条件过长, 并且不容易理解
      //      *  方案二:
      //      *    条件: 给themeTops最后添加一个很大的值, 用于和最后一个主题的top进行比较.
      //      *    优点: 简洁明了, 便于理解
      //      *    缺点: 需要引入一个较大的int数字
      //      * 疑惑: 在第一个判断中, 为什么不能直接判断(currentPos >= iPos)即可?
      //      * 解答: 比如在某一个currentPos大于第0个时, 就会break, 不会判断后面的i了.
      //      */
      //     if (position >= iPos && position < this.themeTopYs[i+1]) {
      //       if (this.currentIndex !== i) {
      //         this.currentIndex = i;
      //       }
      //       break;
      //     }
      //     // if (this.currentIndex !== i && ((i < length - 1 && positionY >= this.themeTopYs[i] && positionY < this.themeTopYs[i+1]) || (i === length - 1 && positionY >= this.themeTopYs[i] ))) {
      //     //   this.currentIndex = i;
      //     //   console.log(this.currentIndex)
      //     // }
      //   }
      // },

      backClick (){
        // 通过refs拿到上面的scroll组件对象，通过对象获取scroll属性，再调用属性方法
        this.$refs.scroll.scrollTo(0, 0, 300)
      },

      // 点击添加到购物车
      addToCart() {
        // 1.获取商品信息（购物车需要的信息）
        const product = {}
        product.image = this.topImages[0]
        product.title = this.goods.title
        product.desc = this.goods.desc
        product.price = this.goods.nowPrice
        product.iid = this.iid

        // 2.将商品添加到购物车(在store里操作数据)
        // this.$store.cartList.push(product) 方法一
        // this.$store.commit('addCart', product)  方法二
        // this.$store.dispatch('addCart', product).then(res => {   方法三
        //   console.log(res)
        // })
        // 方法四：使用actions映射
        this.addCart(product).then(res => {
          // 添加购物车成功后弹出Toast
          this.$toast.show(res, 1500)

        })
      }
    },
    created(){
      // 1.获取商品 iid并保存
      this.iid = this.$route.params.iid
      // 2.根据iid请求详情数据
      getDetail(this.iid).then(res => {
        const data = res.result
        // 1.获取顶部轮播图数据
        this.topImages = data.itemInfo.topImages
        // 2.获取商品信息
        this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services)
        // 3.获取店铺信息
        this.shop = new Shop(data.shopInfo)
        // 4.获取商品详细信息
        this.detailInfo = data.detailInfo
        // 5.获取参数信息
        this.paramInfo = new GoodsParam(data.itemParams.info, data.itemParams.rule)
        // 6.取出评论信息
        if (data.rate.cRate !== 0) {
          this.commentInfo = data.rate.list[0]
        }

      })
      // 3.请求推荐数据
      getRecommend().then(res => {
        this.recommends = res.data.list
      })

      // this.getThemeTopY = debounce(() => {
      //   this.themeTopYs = []
      //   this.themeTopYs.push(0)
      //   this.themeTopYs.push(this.$refs.param.$el.offsetTop)
      //   this.themeTopYs.push(this.$refs.comment.$el.offsetTop)
      //   this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
      //   console.log(this.themeTopYs)
      // }, 100)
    },
    updated() {

    },
    mounted() {
      const refresh = debounce(this.$refs.scroll.refresh, 500)
      this.$bus.$on('detailItemImgLoad', () => {
        refresh()
      })

    },
    activated() {

    },
  }
</script>

