<template>
  <div id="details">
    <nav-bar>
      <div slot="left" class="back" @click="backClick">
        <img src="~assets/img/common/back.svg" alt="">
      </div>
      <div slot="center" class="title">
        <div v-for="(item, index) in titles"
             class="title-item"
             :class="{active: index === currentIndex}"
             @click="titleClick(index)"
             :key="index">
          {{ item }}
        </div>
      </div>
      <div slot="right" class="to-cart" @click="toCart">
        <span>购</span>
      </div>
    </nav-bar>
  </div>
</template>

<script>
  import NavBar from 'components/common/navbar/NavBar'

  export default {
    name: "DetailNavBar",
    components: {
      NavBar,
    },
    data () {
      return {
        titles: ['商品', '参数', '评论', '推荐'],
        currentIndex: 0,
      }
    },
    methods: {
      titleClick(index) {
        this.currentIndex = index
        // 传出index
        this.$emit('titleClick', index)
      },
      backClick () {
        this.$router.go(-1)
      },
      toCart () {
        this.$router.replace('/shotcart')
      }
    }
  }
</script>

<style scoped>
  .title {
    display: flex;
  }
  .title-item {
    flex: 1;
    font-size: 16px;
  }
  .active {
    color: var(--color-tint)
  }
  .back img {
      margin-top: 12px;
  }
  .to-cart {
    background-color: rgba(250, 128, 114, 0.53);
    border-radius: 100%;
    margin: 10px;
    margin-top: -1px;
  }
  .to-cart span {
    color: #fff
  }
</style>
